body, html {
  height: 100%;
}

body {
  font: 14px "Century Gothic", Futura, sans-serif;
  background: cornflowerblue;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: rgba(255,255,255,0.2);
  border: none;
  float: left;
  font-weight: bold;
  line-height: 34px;
  margin: 2px;
  padding: 0;
  text-align: center;
  -webkit-transition: 0.5s all;
  -o-transition: 0.5s all;
  transition: 0.5s all;
}

.square:hover {
  background: rgba(255,255,255,0.5);
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.game-info {
  margin-left: 20px;
}



